import styled, {keyframes} from 'styled-components';
import Inner from '../Commons/Inner';
import React, {useState} from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-cards';

import './styles.css';

// import required modules
import { EffectCards } from 'swiper/modules';


const bounceAnimation = keyframes`
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px); /*  4px  */
  }
`;

const basePath = [
  './images/weMap/',
  './images/BuzzerBeater/',
  './images/lettuce/',
]
const lettucePath =[
  basePath[2] + 'thumb1.png',
  basePath[2] + 'thumb2.png',
  basePath[2] + 'thumb3.png',
  basePath[2] + 'thumb4.png',
  basePath[2] + 'thumb5.png',
  basePath[2] + 'thumb6.png',
]
const weMapPath = [
  basePath[0] +  'thumb1.png',
  basePath[0] +  'thumb2.png',
  basePath[0] +  'thumb3.png',
  basePath[0] +  'thumb4.png',
  basePath[0] +  'thumb5.png',
  basePath[0] +  'thumb6.png',
  basePath[0] +  'thumb7.png',
  basePath[0] +  'thumb8.png',
]
const buzzerBeaterPath = [
  basePath[1] +  'thumb1.png',
  basePath[1] +  'thumb2.png',
  basePath[1] +  'thumb3.png',
  basePath[1] +  'thumb4.png',
  basePath[1] +  'thumb5.png',
  basePath[1] +  'thumb6.png',
  basePath[1] +  'thumb7.png',
  basePath[1] +  'thumb8.png',
  basePath[1] +  'thumb9.png',
  basePath[1] +  'thumb10.png',
  basePath[1] +  'thumb11.png',
  basePath[1] +  'thumb12.png',
  basePath[1] +  'thumb13.png',
]
const moreAboutPath=[
  './images/githubIcon.png',
  './images/rotate.png',
]
function ProjectImages({ imgPath, row }) {
  return (
    <>
      {row ? (
        <Swiper
          effect={'cards'}
          grabCursor={true}
          modules={[EffectCards]}
          className="mySwiper row"
          autoHeight={false}
        >
          {imgPath.map((item, idx) => (
            <SwiperSlide key={idx}>
              <img className='row' src={item} alt={`weMap-${idx}`} />
            </SwiperSlide>
          ))}
        </Swiper>
      ) : (
        <Swiper
          effect={'cards'}
          grabCursor={true}
          modules={[EffectCards]}
          className="mySwiper"
          autoHeight={false}
        >
          {imgPath.map((item, idx) => (
            <SwiperSlide key={idx}>
              <img src={item} alt={`weMap-${idx}`} />
            </SwiperSlide>
          ))}
        </Swiper>
      )}
    </>
  );
}



const Container = styled.div`
  width: 100%;
  /*   */
  /* height: 1000px; */
  background-color: #91C7EE;
  padding: 100px 0;
  box-sizing: border-box;
`;
const Title = styled.div`
  width: 100%;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 80px;

  & img{
    width: 50px;
  }
`;
const ProjectContainer = styled.div`
  display: flex;
  justify-content: space-around;

  @media (max-width: 740px){
    flex-direction: column;
    align-items: center;
  }
`
// WhiteBox
const WhiteBoxContainer = styled.div`
  width: 100%;
  height: ${(props)=>(props.row === true? (`1000px`):('735px'))};
  margin-bottom: 30px;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  z-index: 0;
  position: relative;

  @media (max-width: 1000px) {
  height: ${(props)=>(props.row === true? (`1000px`):('920px'))};

    font-size: 24px;
  }

  @media (max-width: 740px) {
    width: 100%;
    height: ${(props)=>(props.row === true? (`1000px`):('1520px'))};
    font-size: 36px;
  }
`
const WhiteBox =styled.div`
    width: 100%;
    padding: 24px;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 15px;
    margin-top: 30px;
    display: flex;
    flex-direction: ${(props)=>(props.row ? 'column' : 'row')};
    justify-content: space-around;
    align-items: center;
    gap: 20px;
    position: relative;
    
    /* 3D   */
    transform: ${(props) => (props.isBack ? 'rotateY(180deg)' : 'rotateY(0deg)')};
    opacity: ${(props) => (props.isBack ? '0' : '1')};
    transition: 1s ease; /*       */
    visibility: ${(props) => (props.isBack ? 'hidden' : 'visible')};

    @media (max-width: 740px) {
      padding-top: 80px;
      flex-direction: column;
      justify-content: center;
      gap: 0;

    }
 `

const WhiteBox__back =styled.div`
  width: 100%;
  padding: 24px;
  box-sizing: border-box;
  background-color: #fff;
  align-items: center;
  border-radius: 15px;
  margin-top: 30px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  position: absolute;
  top: 0;
  backface-visibility: hidden;
  @media (max-width: 1000px) {
    flex-direction: column
  }

  /* 3D   */
  transform: ${(props) => (props.isBack ? 'rotateY(0deg)' : 'rotateY(-180deg)')};
  opacity: ${(props) => (props.isBack ? '1' : '0')};
  transition: 1s ease; /*       */
  visibility: ${(props) => (props.isBack ? 'visible' : 'hidden')};
  

`
const Architecture = styled.div`
  width: 80%;
  height: 100%;
  & img{
    width: 100%;
    object-fit: contain;
  }
  @media (max-width: 1000px) {
    margin-top: 48px;
    width: 90%;
  }
`
const ProjThumnailContainer = styled.div`
  width: ${(props)=>(props.row? '90%' : '25%')};
  transform: ${(props) => (props.isBack ? 'rotateY(180deg)' : 'rotateY(0deg)')};
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  transition: .4s;
  
  @media (max-width: 1000px) {
    width: ${(props)=>(props.row? '90%' : '33%')};
    
  }
  @media (max-width: 740px) {
    width: ${(props)=>(props.row? '100%' : '60%')};
  } 
`
const ProjThumnail = styled.div`
  width: 94%;
  height: 90%;

  /* overflow: hidden; */

`
const ProjDetail = styled.div`
    width: ${(props)=>(props.row? '90%' : '44%')};
    height: 100%;
    font-size: 17px;
    padding-top: 48px;
    text-align: start;
    display: flex;
    gap: 20px;

    & strong{
    color: #bb0000;
    }
    & h3{
      font-size: 22px;
      margin-bottom: 1px;
      padding: 0;

      
    }
    
    @media (max-width: 1000px) {
      font-size: 15px;
      height: 90%;

      & h3{
        font-size: 19px;
      }
    }
    @media (max-width: 740px) {
      width: 90%;
      display: block;
    }
`
const ProjDetail_back = styled.div`
    width: ${(props)=>(props.row? '90%' : '44%')};
    height: 100%;
    font-size: 17px;
    padding-top: 48px;
    text-align: start;
    display: flex;
    gap: 20px;

    & strong{
    color: #bb0000;
    }
    & h3{
      font-size: 22px;
      margin-bottom: 1px;
      padding: 0;
    }

    @media (max-width: 1000px) {
    width: 90%;
    padding-top: 0;
    gap: 0;

  }
`
const MoreAbout = styled.div`
    display: flex;
    width: 15%;
    height: 36px;
    position: absolute;
    top: 24px;
    right: 10px;
    gap: 8px;
  & img {
    height: 100%;
    object-fit: cover;
    cursor: pointer;
  }
  animation: ${bounceAnimation} 1s infinite;
`;

const ProjectSection = React.forwardRef((props, ref)=>{
  const [isBack, setIsBack] = useState([false, false, false]);

  const handleIsBack = (idx) => {
    setIsBack((prevIsBack) => prevIsBack.map((val, i) => (i === idx ? !val : val)));
    };
    return (
      <Container ref={ref}>
        <Inner>
          <Title> {' '}<img src="./images/Group.png" alt=" " />Project</Title>
          {/*   */}
          <ProjectContainer>
            <WhiteBoxContainer row={true}>
              <WhiteBox isBack={isBack[0]} row={true}>
                <MoreAbout>
                  <a href="https://github.com/gyural/lettuce-frontend"><img src={moreAboutPath[0]}alt=" " /></a>
                  <div onClick={()=>{handleIsBack(0)}}>
                    <img src={moreAboutPath[1]} alt=" " />
                  </div>
                </MoreAbout>
                <ProjThumnailContainer row={true}>
                  
                  <ProjThumnail><ProjectImages row={true} imgPath={lettucePath}></ProjectImages></ProjThumnail>
                </ProjThumnailContainer>
                <ProjDetail row={true}>
                  
                  <div className='left'>
                    <p>
                      <h3> :<br/></h3>
                      23 7/31 ~ 23 9/3<br/>
                      <h3>   :<br/></h3>
                      ChatGPT API     <br/>
                              <br/>
                         <br/>
                      <h3> :<br/></h3>
                        FE : ReactJS html, css, JS <br/>
                        BE : Python, DRF<br/>
                        Etc : ChatGPTAPI, Naver OCR API,  API,  <br/>
                    </p>
                  </div>
                  <div className='right'>
                    <p>
                      <h3>  :<br/></h3>
                           AI     <br/>
                      <h3> :<br/></h3>
                        <br/><br/>
                    </p>
                  </div>
                </ProjDetail>

              </WhiteBox>
              <WhiteBox__back isBack={isBack[0]}>
                  <MoreAbout>
                    <a href="https://github.com/gyural/lettuce-frontend"><img src={moreAboutPath[0]}alt=" " /></a>
                    <div onClick={()=>{handleIsBack(0)}}>
                      <img src={moreAboutPath[1]} alt=" " />
                    </div>
                  </MoreAbout>
                  <Architecture><img src="./images/lettuce/architecture.png" alt="architecture"/></Architecture>
                  <ProjDetail_back>
                    <p>
                      <h3>     :<br/></h3>
                      <strong>React Component</strong>  UI . <br /><strong> API</strong>     100   .
                         ,     OpenAPI   <strong> </strong>     .
                               .  <br />              
                        . 
                    </p>
                  </ProjDetail_back>
              </WhiteBox__back>
            </WhiteBoxContainer>
          </ProjectContainer>

          {/* WEMAP  */}
          <ProjectContainer>
            <WhiteBoxContainer>
              <WhiteBox isBack={isBack[1]}>
                <MoreAbout>
                    <a href="https://github.com/gyural/WeMap-Frontend"><img src={moreAboutPath[0]}alt=" " /></a>
                    <div onClick={()=>{handleIsBack(1)}}>
                      <img src={moreAboutPath[1]} alt=" " />
                    </div>
                </MoreAbout>
                <ProjThumnailContainer>
                  WeMap
                  <ProjThumnail><ProjectImages imgPath={weMapPath}></ProjectImages></ProjThumnail>
                </ProjThumnailContainer>
                <ProjDetail>
                  
                  
                  <p>
                  <h3> :<br/></h3>
                  23 9/4 ~ 23 10/10<br/>
                  <h3>  :<br/></h3>
                      131      <br/><br/>

                  <h3>   :<br/></h3>
                      <br/>
                    /  /    <br/>
                     <br/><br/>

                  <h3> :<br/></h3>
                    <br/><br/>

                  <h3> :<br/></h3>
                    FE : React <br/>
                    BE : DRF, AWS Lambda<br/>
                    Etc : kakaoMap, kakao mobilitySGIS,   ,   <br/>
                  </p>
                </ProjDetail>

              </WhiteBox>
              <WhiteBox__back isBack={isBack[1]}>
                    <MoreAbout>
                      <a href="https://github.com/gyural/WeMap-Frontend"><img src={moreAboutPath[0]}alt=" " /></a>
                      <div onClick={()=>{handleIsBack(1)}}>
                        <img src={moreAboutPath[1]} alt=" " />
                      </div>
                    </MoreAbout>
                  <Architecture><img src="./images/weMap/Architecture.png" alt="architecture"/></Architecture>
                  <ProjDetail_back>
                    
                      <p>
                        <h3>    :<br/></h3>
                            .<br/>
                        <strong>webSocket</strong>       .<br/>
                               <strong>kakaoMap API</strong>     . 
                            <strong>SGIS API</strong>    4      .<br/> 
                            kakaoMap     <strong>Proj4</strong>      .<br/> 
                        <strong>JWT</strong>    .   .    cookie CORS    . 
                      </p>
                  </ProjDetail_back>
              </WhiteBox__back>
            </WhiteBoxContainer>
          </ProjectContainer>

          {/* BUZZERBEATER  */}
          <ProjectContainer>
            <WhiteBoxContainer>
              <WhiteBox isBack={isBack[2]}>
                <MoreAbout>
                  <a href="https://github.com/TEAM-REDCODE/buzzer-beater-frontend"><img src={moreAboutPath[0]}alt=" " /></a>
                  <div onClick={()=>{handleIsBack(2)}}>
                    <img src={moreAboutPath[1]} alt=" " />
                  </div>
                </MoreAbout>
                <ProjThumnailContainer>
                  BuzzerBeater
                  <ProjThumnail><ProjectImages imgPath={buzzerBeaterPath}></ProjectImages></ProjThumnail>
                </ProjThumnailContainer>
                <ProjDetail>
                  <p>
                  <h3> :<br/></h3>
                  23 11/27 ~ 24 2/2<br/>
                  <h3>  :<br/></h3>
                         <br/><br/>

                  <h3>   :<br/></h3>
                       <br/>
                      <br/>
                       <br/><br/>

                  <h3> :<br/></h3>
                    <br/><br/>

                  <h3> :<br/></h3>
                    FE : ReactNative <br/>
                    BE : NodeJS, express, mySQL<br/>
                    Etc : JWT, expo <br/>
                  </p>
                </ProjDetail>

              </WhiteBox>
              <WhiteBox__back isBack={isBack[2]}>
                  <MoreAbout>
                    <a href="https://github.com/TEAM-REDCODE/buzzer-beater-frontend.git"><img src={moreAboutPath[0]}alt=" " /></a>
                    <div onClick={()=>{handleIsBack(2)}}>
                      <img src={moreAboutPath[1]} alt=" " />
                    </div>
                  </MoreAbout>
                  <Architecture><img src="./images/BuzzerBeater/architechture.png" alt="architecture"/></Architecture>
                  <ProjDetail_back>
                    <p>
                      <h3>    :<br/></h3>
                      <strong>JWT </strong>         API  . 
                          . 
                      <strong>expo</strong>   ReactNative      .<br/>
                      <strong>axios</strong>   DB Get/Post/Delete/Put  API .
                      <strong>reactNavigation</strong>    . <strong>contextAPI</strong>   
                              .
                       <strong>PlayConsole</strong>   .( )

                    </p>
                  </ProjDetail_back>
              </WhiteBox__back>
            </WhiteBoxContainer>
          </ProjectContainer>
          
        </Inner>
      </Container>
    )
  }

) 

export default ProjectSection